import request from '@/utils/request'

export function fetchList() {
    return request({
        url: '/blog/findAllBlogs',
        method: 'get'
    })
}

export function getBlog(id) {
    return request({
        url: '/blog/'+id,
        method: 'get'
    })
}

export function getTags(){
    return request({
        url: '/blog/tag/getTags',
        method: 'post'
    })
}

export function search(title){
    return request({
        baseURL: '/blog/es/search?title='+title,
        method: 'get',
    })
}
